  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 5px;
}
.Rescue_Batches {
  width: 100%;
  display: flex;
  /* justify-content: center; */
  flex-direction: column;
  align-items: center;
  margin-top: 15px;
}
.Rescue-Batches {
  width: 90%;
  /* height: 100%; */
  /* display: flex;
        justify-content: center; */
  /* background: #f0f0f0; */
}
._Batches {
  margin-top: 10px;
}
.collect {
  width: 100%;
  height: 40px;
  background: rgba(242, 155, 118, 0.7);
  border-radius: 28px;
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
}
.custopen {
  width: 100%;
}
.tab_custopen {
  height: 200px;
  overflow-y: auto;
}
.custcollect {
  display: none;
}
.tr_top {
  width: 100%;
  height: 35px;
  background: #e0ecff;
  border-radius: 4px 4px 0px 0px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.tr_top > th {
  font-size: 14px;
  color: #333333;
  width: 85px;
  text-align: center;
  padding-left: 10px;
  padding-right: 10px;
}
.tr_top > th:nth-child(2) {
  width: 70px !important;
}
.tr_top > th:nth-child(4) {
  width: 90px !important;
}
.tr_conts {
  width: 100%;
  height: 65px;
  background-color: #ffffff;
  display: flex;
  justify-content: space-around;
  align-items: center;
  /* border-bottom: 1px solid #f3f5f9; */
}
.tr_conts > td {
  font-size: 12px;
  color: #666666;
  width: 85px;
  text-align: center;
  padding-left: 2.5px;
  padding-right: 2.5px;
  font-weight: 800;
}
.tr_conts > td:nth-child(2) {
  width: 70px !important;
}
.tr_conts > td:nth-child(4) {
  width: 90px !important;
}
.tr_conts:nth-child(2n) {
  background: #f3f5f9;
}
.open {
  width: 100%;
  height: 40px;
  background: rgba(242, 155, 118, 0.7);
  border-radius: 28px 28px 0px 0px;
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  opacity: 0.7;
}
.batch {
  color: #fff;
  font-size: 13px;
  margin-left: 20px;
}
.iconduo {
  color: #f29b76;
  font-size: 0.8rem;
  /* position: absolute;
  right: 1.5rem; */
  margin-left: 10px;
}
.bottom_banner {
  width: 100%;
  height: 150px;
  background: url("../../static/images/bottomBanner.png") no-repeat;
  background-size: 100% 100%;
  position: relative;
  margin-top: 10px;
  margin-bottom: 10px;
}
.message_conts {
  width: 100%;
  background: #fff;
  margin-bottom: 90px;
}
.mage_cont {
  width: 100%;
  height: 40px;
  border-bottom: 1px solid #fafafa;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 15px;
}
.new_mage {
  font-size: 20px;
  color: #333333;
  padding-left: 16px;
}
.mages_num {
  font-size: 15px;
  color: #999999;
  padding-right: 12px;
}
.user_mages {
  width: 100%;
  /* height: 200px; */
  /* margin-bottom: 2.5rem; */
  overflow: auto;
}
.userMage {
  width: 100%;
  height: 105px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.user_image {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  /* overflow: hidden; */
  margin-top: 10px;
  margin-left: 10px;
}
.user_image img {
  width: 100%;
  height: 100%;
}

.user-Mage {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  padding-left: 20px;
}
.user_name {
  padding-top: 10px;
  font-size: 14px;
  color: #333333;
}
.user_date {
  padding-top: 2px;
  font-size: 12px;
  color: #999999;
}
.user_conts {
  padding-top: 14px;
  font-size: 14px;
  color: #333333;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
  padding-right: 34px;
  letter-spacing: 2px;
  position: relative;
}
.hideuser_conts {
  padding-top: 14px;
  font-size: 14px;
  color: #333333;
  padding-right: 34px;
  letter-spacing: 2px;
  position: relative;
}
.Open_user_conts {
  font-size: 12px;
  color: #005aff;
  position: absolute;
  right: 10px;
  bottom: 0;
}
.suspension {
  width: 100%;
  /* height: 79px; */
  position: fixed;
  /* background: #fff; */
  bottom: 20px;
  display: flex;
  justify-content: space-around;
}
.likes {
  width: 105px;
  height: 30px;
  background-color: #ffb4b4;
  border-radius: 28px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  color: #fff;
}
.iconxin- {
  color: #ff0000;
  padding-right: 12px;
}
.iconxin {
  color: #ff0000;
  padding-right: 12px;
}
.share {
  width: 105px;
  height: 30px;
  background-color: #95bdfc;
  border-radius: 28px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  color: #fff;
}
.iconzhuanfa1 {
  color: #fff;
  padding-right: 5px;
}
.DoctorData {
  width: 105px;
  height: 30px;
  background-color: #f19149;
  border-radius: 28px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  color: #fff;
}
</style>



